<template>
  <div>
    <section class="block1 block1-short block1-shop">
      <img v-if="this.$vssWidth > 1170" src="/static/img/shop/1/bg.png" alt="" class="bg">
      <img v-if="this.$vssWidth <= 1170" src="/static/img/shop/1/bg-m.png" alt="" class="bg">
      <div class="container">
        <h1>Профессиональная <br>косметика для ухода <br>за волосами</h1>
        <ul class="flex-start align-center">
          <li>Отправка заказа в тот же день</li>
          <li>В наличии на складе</li>
          <li>Доступные цены</li>
          <li>Оптовые скидки</li>
        </ul>
        <button @click="goto('shop')" class="btn">Смотреть продукцию</button>
      </div>
    </section>
    <section ref="shop" class="shop">
      <div class="container">
        <ToBasketLink v-if="this.$vssWidth <= 1170"></ToBasketLink>
        <div class="tabs" v-if="this.$vssWidth > 1170 && !categoriesLoading">
          <div
              :class="[
                  'tab',
                  {'active': category.id === currentCategoryId}
              ]"
              v-for="category in categories"
              :key="category.id"
              @click="currentCategoryId = category.id"
          >
            <span>{{ category.name }}</span>
          </div>
        </div>
        <VueSlickCarousel
            v-if="this.$vssWidth <= 1170 && !categoriesLoading"
            class="tabs"
            :arrows="false"
            :dots="false"
            :draggable="true"
            :swipe="true"
            :variableWidth="true"
            :adaptiveHeight="true"
        >
          <div
              :class="[
                  'tab',
                  {'active': category.id === currentCategoryId}
              ]"
              v-for="category in categories"
              :key="category.id"
              @click="currentCategoryId = category.id"
          >
            <span>{{ category.name }}</span>
          </div>
        </VueSlickCarousel>
        <Loader class="loader" v-if="categoriesLoading"></Loader>

        <div class="shop-flex flex-between align-start">
          <div class="shop-products__wrapper">
            <div
                :class="[
                    'shop-products',
                    {'shop-products-loading': productsLoading}
                    ]"
            >
              <Loader class="products-loader" v-if="productsLoading"></Loader>
              <ProductCard
                  v-for="product in products"
                  :key="product.id"
                  :product="product"
              ></ProductCard>
            </div>

            <div v-if="nextPage !== -1 && products.length > 0" @click="getProducts" class="show-button flex-center align-center">
              <span>Показать еще</span>
              <div class="wrap">
                <svg width="12" height="10" viewBox="0 0 12 10" fill="none" xmlns="http://www.w3.org/2000/svg">
                  <path d="M11.168 4.81706L1.16797 4.81706" stroke="#22252C" stroke-width="1.5" stroke-linecap="round"
                        stroke-linejoin="round"/>
                  <path d="M7.13477 0.800829L11.1681 4.81683L7.13477 8.8335" stroke="#22252C" stroke-width="1.5"
                        stroke-linecap="round" stroke-linejoin="round"/>
                </svg>
              </div>
            </div>
          </div>
          <Basket v-if="this.$vssWidth > 1170"></Basket>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import VueSlickCarousel from 'vue-slick-carousel'
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
import ProductCard from "@/components/ProductCard";
import Basket from "@/components/Basket/index";
import ToBasketLink from "@/components/ToBasketLink";
import Loader from "@/components/Loader";
import api from "@/api"

export default {
  name: "Shop",
  components: {VueSlickCarousel, ProductCard, Basket, ToBasketLink, Loader},
  data: () => ({
    categoriesLoading: false,
    productsLoading: false,
    currentCategoryId: null,
    nextPage: null,
    categories: [],
    products: []
  }),
  methods: {
    goto(refName) {
      const top = this.$refs[refName].offsetTop;
      window.scrollTo(0, top);
    },
    async getCategories() {
      this.categoriesLoading = true
      this.categories = await api.getShopCategories()
      this.currentCategoryId = this.categories[0].id
      this.categoriesLoading = false
    },
    async getProducts() {
      this.productsLoading = true
      const {data, next_page} = await api.getProducts(this.currentCategoryId, this.nextPage)
      this.products.push(...data)
      this.nextPage = next_page
      this.productsLoading = false
    }
  },
  watch: {
    currentCategoryId() {
      if (this.currentCategoryId !== null) {
        this.products.length = 0
        this.nextPage = null
        this.getProducts()
      }
    }
  },
  created() {
    this.getCategories()
  }
}
</script>

<style scoped>
.loader {
  margin-bottom: 40px;
}

.show-button {
  margin-top: 60px;
}

.products-loader {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: rgba(0, 0, 0, .2);
  z-index: 10;
}

.shop-products {
  position: relative;
}

.shop-products-loading {
  opacity: .3;
}
</style>